import {Injectable} from '@angular/core';
import {Category} from '../model/Category';
import {Task} from '../model/Task';
import {Observable} from 'rxjs';
import {TaskDAOArray} from '../data/dao/implementation/TaskDAOArray';
import {CategoryDAOArray} from '../data/dao/implementation/CategoryDAOArray';
import {Priority} from '../model/Priority';
import {PriorityDAOArray} from '../data/dao/implementation/PriorityDAOArray';

@Injectable({
  providedIn: 'root'
})
export class DataHandlerService {

  // tasksSubject = new BehaviorSubject<Task[]>(TestData.tasks);
  // categoriesSubject = new BehaviorSubject<Category[]>(TestData.categories);
  private taskDaoArray = new TaskDAOArray();
  private categoryDaoArray = new CategoryDAOArray();
  private priorityDAOArray = new PriorityDAOArray();

  constructor() {
  }

  /*  fillCategories(): Category[] {
      return TestData.categories;
    }*/

  /* fillTasks() {
     this.tasksSubject.next(TestData.tasks);
   }

   fillTasksByCategory(category: Category) {
     const tasks = TestData.tasks.filter(task => task.category === category);
     this.tasksSubject.next(tasks);
   }
 */
  getAllTasks(): Observable<Task[]> {
    return this.taskDaoArray.getAll();
  }

  getAllCategories(): Observable<Category[]> {
    return this.categoryDaoArray.getAll();
  }

  getAllPriorities(): Observable<Priority[]> {
    return this.priorityDAOArray.getAll();
  }

  searchTasks(category: Category, searchText: string, status: boolean, priority: Priority) {
    return this.taskDaoArray.search(category, searchText, status, priority);
  }

  updateTask(task: Task): Observable<Task> {
    return this.taskDaoArray.update(task);
  }

  deleteTask(id: number): Observable<Task> {
    return this.taskDaoArray.delete(id);
  }

  updateCategory(category: Category): Observable<Category> {
    return this.categoryDaoArray.update(category);
  }

  deleteCategory(id: number): Observable<Category> {
    return this.categoryDaoArray.delete(id);
  }

  addTask(task: Task): Observable<Task> {
    return this.taskDaoArray.add(task);
  }

  addCategory(title: string): Observable<Category> {
    return this.categoryDaoArray.add(new Category(null, title));
  }

  searchCategories(title: string): Observable<Category[]> {
    return this.categoryDaoArray.search(title);
  }


  getCompletedTasksCountInCategory(category: Category): Observable<number> {
    return this.taskDaoArray.getCompletedCountInCategory(category);
  }

  getUncompletedTotalCount(): Observable<number> {
    return this.taskDaoArray.getTotalUncompletedCount();
  }

  getUncompletedTasksCountInCategory(category: Category): Observable<number> {
    return this.taskDaoArray.getUncompletedCountInCategory(category);
  }

  getTotalCountInCategory(category: Category): Observable<number> {
    return this.taskDaoArray.getTotalCountInCategory(category);
  }

  getTotalCount(): Observable<number> {
    return this.taskDaoArray.getTotalCount();
  }

  deletePriority(id: number): Observable<Priority>{
    return this.priorityDAOArray.delete(id);
  }

  addPriority(priority: Priority): Observable<Priority> {
    return this.priorityDAOArray.add(priority);
  }

  updatePriority(priority: Priority): Observable<Priority> {
    return this.priorityDAOArray.update(priority);
  }

}

