import {CategoryDAO} from '../interface/CategoryDAO';
import {Observable, of} from 'rxjs';
import {Category} from '../../../model/Category';
import {TestData} from '../../TestData';

export class CategoryDAOArray implements CategoryDAO {

  getAll(): Observable<Category[]> {
    return of(TestData.categories);
  }

  add(category: Category): Observable<Category> {
    if (category.id === null || category.id === 0) {
      category.id = this.getLastIdCategory();
    }
    TestData.categories.push(category);
    return of(category);
  }

  private getLastIdCategory(): number {
    return Math.max.apply(Math, TestData.categories.map(c => c.id)) + 1;
  }

  delete(id: number): Observable<Category> {
    TestData.tasks.forEach(task => {
      if (task.category && task.category.id === id) {
        task.category = null;
      }
    });
    const categoriesTmp = TestData.categories.find(t => t.id === id);
    TestData.categories.splice(TestData.categories.indexOf(categoriesTmp), 1);
    return of(categoriesTmp);
  }

  get(id: number): Observable<Category> {
    return undefined;
  }


  search(title: string): Observable<Category[]> {
   return of(
     TestData.categories.filter(
       cat => cat.title.toUpperCase().includes(title.toUpperCase())).
       sort((c1, c2) => c1.title.localeCompare(c2.title))
   );
  }

  update(category: Category): Observable<Category> {
    const categoriesTmp = TestData.categories.find(t => t.id === category.id);
    TestData.categories.splice(TestData.categories.indexOf(categoriesTmp), 1, category);
    return of(categoriesTmp);
  }

}
