import {Injectable} from '@angular/core';
import * as introJs from 'intro.js/intro';

@Injectable({
  providedIn: 'root'
})
export class IntroService {

  introJS = introJs();

  constructor() {
  }

  // показать интро (справку) с подсветкой элементов
  public startIntroJS() {

    this.introJS.setOptions(
      {
        nextLabel: 'след. >',
        prevLabel: '< пред.',
        doneLabel: 'Выход',
        skipLabel: 'Выход',
        exitOnEsc: true,
        exitOnOverlayClick: false
      });

    this.introJS.start();

  }

}
