import {Component, OnInit} from '@angular/core';
import {Task} from './model/Task';
import {DataHandlerService} from './service/data-handler.service';
import {Category} from './model/Category';
import {Priority} from './model/Priority';
import {zip} from 'rxjs';
import {IntroService} from './service/intro.service';
import {concatMap, map} from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'my-plan';
  tasks: Task[];
  categories: Category[];
  priorities: Priority[];
  categoryMap = new Map<Category, number>();

  selectedCategory: Category = null;

  searchTaskText = '';
  searchCategoryText = '';
  statusFilter: boolean;
  priorityFilter: Priority = null;


  totalTasksCountInCategory: number;
  completedTasksCountInCategory: number;
  uncompletedTasksCountInCategory: number;
  uncompletedTotalTasksCount: number;

  showStat = true;

  constructor(
    private dataHandler: DataHandlerService,
    private introService: IntroService
  ) {

  }

  ngOnInit(): void {
    this.dataHandler.getAllCategories().subscribe(categories => this.categories = categories);
    this.dataHandler.getAllPriorities().subscribe(priorities => this.priorities = priorities);
    this.onSelectCategory(null);
    this.introService.startIntroJS();
    this.fillCategories();
  }

  onSelectCategory(category: Category) {
    this.selectedCategory = category;
    this.updateTasksAndStat();
  }

  onUpdateTask(task: Task) {
    this.dataHandler.updateTask(task).subscribe(() => {
      this.fillCategories();
      this.updateTasksAndStat();
    });
  }

  onDeleteTask(task: Task) {
    this.dataHandler.deleteTask(task.id).pipe(
      concatMap(t => {
          return this.dataHandler.getUncompletedTasksCountInCategory(t.category).pipe(map(count => {
            return ({t: task, count});
          }));
        }
      )).subscribe(result => {


      const t = result.t as Task;

      if (t.category) {
        this.categoryMap.set(t.category, result.count);
      }

      this.updateTasksAndStat();


    });
  }

  onDeleteCategory(category: Category): void {
    this.dataHandler.deleteCategory(category.id).subscribe(() => {
      this.selectedCategory = null;
      this.onSelectCategory(this.selectedCategory);
      this.categoryMap.delete(category);
      this.updateTasks();
    });
  }

  onUpdateCategory(category: Category): void {
    this.dataHandler.updateCategory(category).subscribe(() => {
      this.onSearchCategory(this.searchCategoryText);
    });
  }

  onFilterTasksByStatus(status: boolean) {
    this.statusFilter = status;
    this.updateTasks();
  }

  onSearchTasks(searchString: string) {
    this.searchTaskText = searchString;
    this.updateTasks();
  }

  updateTasks() {
    this.dataHandler.searchTasks(
      this.selectedCategory,
      this.searchTaskText,
      this.statusFilter,
      this.priorityFilter,
    ).subscribe(tasks => {
      this.tasks = tasks;
    });
  }

  onFilterTasksByPriority(priority: Priority) {
    this.priorityFilter = priority;
    this.updateTasks();
  }

  onAddTask(task: Task) {
    this.dataHandler.addTask(task).pipe(
      concatMap(t => {
          return this.dataHandler.getUncompletedTasksCountInCategory(t.category).pipe(map(count => {
            return ({t: task, count});
          }));
        }
      )).subscribe(result => {

      const t = result.t as Task;

      if (t.category) {
        this.categoryMap.set(t.category, result.count);
      }

      this.updateTasksAndStat();

    });
  }

  onAddCategory(title: string) {
    this.dataHandler.addCategory(title).subscribe(() => this.updateCategories());
  }

  private updateCategories() {
    this.dataHandler.getAllCategories().subscribe(categories => this.categories = categories);
  }

  onSearchCategory(title: string) {
    this.searchCategoryText = title;
    this.dataHandler.searchCategories(title).subscribe(categories => {
      this.categories = categories;
    });
  }

  updateTasksAndStat(): void {
    this.updateTasks();
    this.updateStat();
  }

  updateStat(): void {
    zip(
      this.dataHandler.getTotalCountInCategory(this.selectedCategory),
      this.dataHandler.getCompletedTasksCountInCategory(this.selectedCategory),
      this.dataHandler.getUncompletedTasksCountInCategory(this.selectedCategory),
      this.dataHandler.getUncompletedTotalCount())

      .subscribe(array => {
        this.totalTasksCountInCategory = array[0];
        this.completedTasksCountInCategory = array[1];
        this.uncompletedTasksCountInCategory = array[2];
        this.uncompletedTotalTasksCount = array[3];
      });
  }

  toggleStat(showStat: boolean) {
    this.showStat = !showStat;
  }

  fillCategories() {

    if (this.categoryMap) {
      this.categoryMap.clear();
    }

    this.categories = this.categories.sort((a, b) => a.title.localeCompare(b.title));


    this.categories.forEach(cat => {
      this.dataHandler.getUncompletedTasksCountInCategory(cat).subscribe(count => this.categoryMap.set(cat, count));
    });

  }

}
